import { Button, Drawer, Divider } from 'antd';
import { MenuOutlined, PlusOutlined } from '@ant-design/icons';
import { CountDown } from '../components/CountDown';
import { CreateCard } from '../components/CreateCard';
import { UpdateCard } from '../components/UpdateCard';
import { LessonsList } from '../components/LessonsList';
import { ShowCards } from '../components/ShowCards';
import { useHomeContext } from '../context/HomeContext';

export function Home () {
  const { openMenu, setOpenMenu, setAddCart, addCart, editCard, setEditCard } = useHomeContext();

  return (
    <div className="w-full h-full">
      <CountDown />
      <div className="absolute top-2 right-2 z-10">
        <Button icon={<MenuOutlined />} onClick={() => setOpenMenu(true)}></Button>
      </div>
      <Drawer title="Chn bi hc" onClose={() => setOpenMenu(false)} open={openMenu} size="default">
        <Button icon={<PlusOutlined />} onClick={() => setAddCart(true)}>To bi hc</Button>
        <Divider plain orientation="left" className="uppercase py-4"><span className="font-bold">Bi hc  to</span></Divider>
        <LessonsList />
        {
          editCard ? (
            <Drawer title="Sa bi hc" closable={false} onClose={() => setEditCard(false)} open={editCard}>
              <UpdateCard />
            </Drawer>
          ):(
            <Drawer title="To bi hc" closable={false} onClose={() => setAddCart(false)} open={addCart}>
              <CreateCard />
            </Drawer>
          )
        }
      </Drawer>
      <ShowCards />
    </div>
  )
}
