import React from 'react';
import { useHomeContext } from '../context/HomeContext';
import { Carousel, Card, Result  } from 'antd';
import { SmileOutlined } from '@ant-design/icons';

export function ShowCards() {
  const { lesson } = useHomeContext();

  if(lesson.name){
    const { cards } = lesson;

    return (
      <Carousel infinite={false}>
        {
          cards.map((obj:any) => {
            return (
              <div key={obj.id}>
                <div className="flex items-center justify-center relative h-[100vh] bg-[#80d4ff] text-[#008000]">
                  <div className="absolute w-[70%] h-[75%] bg-[#FFF] rounded-[16px] py-6">
                    <div className="h-full flex flex-col items-center justify-center">
                      {
                        obj.image && (
                          <div className="w-full h-1/2">
                            <img className="w-full h-full object-contain" src={obj.image.url}/>
                          </div>
                        )
                      }
                      <div className="flex items-center justify-center w-full h-1/2 text-[120px]">
                        {obj.word}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            )
          })
        }
        <div>
          <div className="flex items-center justify-center relative h-[100vh] bg-[#364d79]">
            <div className="absolute w-[70%] h-[75%] bg-[#FFF] rounded-[16px] py-6">
              <div className="flex items-center justify-center h-full ">
                <Result
                  icon={<SmileOutlined />}
                  title="Xong ri, gii qu, Good job!"
                />
              </div>
            </div>
          </div>
        </div>
      </Carousel>
    )
  }
  else{
    return (
      <div className="flex items-center justify-center w-full h-full">
        Chn bi hc trong menu nh!
      </div>
    );
  }
}
