import React, { useState, useEffect } from 'react';
import { PlusOutlined, DeleteOutlined } from '@ant-design/icons';
import { Button, Form, Input, Upload, Card, message } from 'antd';
import type { UploadFile, UploadProps } from 'antd';
import cloudinary from '../api/Cloudinary';
import supabase from '../api/Supabase';
import { useHomeContext } from '../context/HomeContext';

export function UpdateCard() {
  const [fileList, setFileList] = useState<any>([]);
  const { editId, setEditCard } = useHomeContext();
  let [detail, setDetail] = useState<any>([]);
  const [isData, setIsData] = useState(false);

  const getCardDetail = async() => {
    let {data} = await supabase.from('cards').select('*').eq('id', editId);
    if(data){
      setDetail(data[0]);
      setIsData(true);

      const { cards } = data[0];
      let fileList:any = [];
      cards.map((obj:any, index:number) => {
        fileList[index] = obj.imageFile;
      });
      setFileList(fileList);
    }
  }

  useEffect(() => {
    getCardDetail();
  }, []);

  const handleChange = (value:any, key:number) => {
    let newFileList = fileList.map((arr:[], index:number) => {
      if (index == key) {
        return value.fileList;
      } else {
        return fileList[index];
      }
    });
    setFileList(newFileList);
  }

  const onFinish = async (values: any) => {
      message.warning('ang sa bi hc...');

      const newLesson:any = {
        name: '',
        cards: []
      };

      newLesson.name = values.name;

      let { cards } = values;

      for(var i=0; i<cards.length; i++){
        let{ image } = cards[i];
        newLesson.cards[i] = {};
        if(image){
          let { fileList } = image;
          if(fileList){
            let { originFileObj } = fileList[0];
            let imageUrl = await cloudinary(originFileObj);
            newLesson.cards[i].image = {'status': 'done', 'url': imageUrl};
            newLesson.cards[i].imageFile = fileList;
          }
        }
        let{ word } = cards[i];
        newLesson.cards[i].word = word;
      }


      const { data, error } = await supabase
        .from('cards')
        .update([newLesson])
        .eq('id', editId)
        .select()

      if(error){
        console.log(error);
      }

      message.success(' sa bi hc thnh cng');
      setEditCard(false);
  };

  return (
    isData ? (
      <Form
        name="dynamic_form_nest_item"
        onFinish={onFinish}
        autoComplete="off"
        initialValues={detail}
      >
        <Form.Item
          name="name"
          rules={[{ required: true, message: 'Nhp tn bi hc' }]}
        >
          <Input placeholder="Tn bi hc"/>
        </Form.Item>
        <Form.List name="cards">
          {(fields, { add, remove }) => (
            <>
              {fields.map(({ key, name, ...restField }) => (
                <Card key={key} hoverable className="my-4" size="small">
                  <div className="flex flex-col">
                    <div className="flex justify-between">
                      <Form.Item
                        {...restField}
                        name={[name, 'word']}
                        rules={[{ required: true, message: 'Nhp t vng' }]}
                      >
                        <Input placeholder="T vng" className="w-[250px]"/>
                      </Form.Item>
                      <Button icon={<DeleteOutlined />} onClick={() => remove(name)} className="bg-[#FF0000] text-[#FFF]"></Button>
                    </div>
                    <Form.Item
                      name={[name, 'image']}
                      rules={[{ required: false}]}
                    >
                      <Upload
                        beforeUpload={() => false}
                        listType="picture-card"
                        onChange={(value) => handleChange(value, key)}
                        maxCount={1}
                        fileList={fileList[key]}
                      >
                        <button style={{ border: 0, background: 'none' }} type="button" >
                          <PlusOutlined />
                          <div className="mt-[8px]">Upload</div>
                        </button>
                      </Upload>
                    </Form.Item>
                  </div>
                </Card>
              ))}
                <Form.Item>
                  <Button type="dashed" onClick={() => add()} block icon={<PlusOutlined />}>
                    Thm card
                  </Button>
                </Form.Item>
            </>
          )}
        </Form.List>
        <Form.Item>
          <Button type="primary" htmlType="submit">
            Lu
          </Button>
        </Form.Item>
      </Form>
    ):
    null
  )
}
