import React, { useState, useEffect } from 'react';
import { Radio, Button } from 'antd';
import { EditOutlined } from '@ant-design/icons';
import type { RadioChangeEvent } from 'antd';
import { useHomeContext } from '../context/HomeContext';

export function LessonsList() {
  const [value, setValue] = useState();
  const { getLesson, setCountDown, setOpenMenu, getLessonsList, lessons, isLessons, editLesson } = useHomeContext();

  useEffect(() => {
    getLessonsList();
  }, [])

  const onChange = (e: RadioChangeEvent) => {
    setValue(e.target.value);
    getLesson(e.target.value);
    setOpenMenu(false);
    setCountDown(true);
  };

  return (
    <div>
      {
        isLessons && (
          <Radio.Group onChange={onChange} defaultValue={value} className="flex flex-col space-y-4">
            {
              lessons.map((obj:any) => {
                return (
                  <div key={obj.id} className="flex items-center justify-between">
                    <Radio key={obj.id} value={obj.id} className="text-[16px]"><span className="uppercase">{obj.name}</span></Radio>
                    <Button icon={<EditOutlined />} onClick={() => editLesson(obj.id)}></Button>
                  </div>
                )
              })
            }
          </Radio.Group>
        )
      }
    </div>
  )
}
